$mainColor: #ff0000;

.red {
  color: $mainColor;
}

.btn-danger {
  margin: 2px;
}

@mixin function {
  background-color: #eeeeee;
  padding: 20px;
  border-radius: 5px;
  max-width: 500px;
  width: 100%;
  margin: auto;
  font-size: 22px;
}

.my-alert {
  @include function();
}

.my-alert-yellow {
  @extend .my-alert;
  background-color: #ffe591;
}

.my-alert p {
  margin-bottom: 0px;
}

